.cards__like {
  margin: 0 0 5px 0;
  padding: 0;
  min-width: 22px;
  min-height: 19px;
  border: none;
  background: transparent url('../../../images/btnsAndicons/like.svg') no-repeat center;
  transition: all 0.4s ease-in-out;
}

.cards__like:hover {
  opacity: 0.5;
  cursor: pointer;
}